import { Component, h, Host, Prop } from '@stencil/core';
import { TpColor } from '../../../utils/types/color.type';
import { createColorClasses } from '../../../utils/functions/color.function';

@Component({
  tag: 'tp-tiles',
  styleUrl: 'tiles.scss',
  shadow: true,
})
export class TpTiles {
  /**
   * todo
   */
  @Prop({ reflect: true }) color?: TpColor;

  /**
   * todo
   */
  @Prop() titulo?: string;

  /**
   * todo
   */
  @Prop() label?: string;

  /**
   * todo
   */
  @Prop() solid = false;

  /**
   * todo
   */
  @Prop({ reflect: true }) selected = false;

  render() {
    const { color, titulo, label, selected, solid } = this;
    return (
      <Host
        class={createColorClasses(color, {
          'tp-tiles': true,
          'tp-tiles--solid': solid,
          'tp-tiles--selected': selected,
        })}
      >
        <div class="tp-tiles__border"></div>
        <div class="tp-tiles__content">
          <ion-text class="tp-tiles__title" innerHTML={titulo}></ion-text>
          <ion-text class="tp-tiles__label" innerHTML={label}></ion-text>
          <slot></slot>
        </div>
      </Host>
    );
  }
}
